// ** Custom Components
import Avatar from '@components/avatar'
import { useEffect, useState } from 'react'
import axios from '@src/configs/axios/axiosConfig'
// ** Reactstrap Imports
import { Card, CardHeader, CardTitle, CardBody, Badge, Row } from 'reactstrap'
import moment from 'moment'
//import { setNotification } from '@src/redux/notifications'
import {  useSelector } from 'react-redux'
//import { activeOrganizationid } from '@src/helper/sassHelper'
//import Pusher from 'pusher-js'
//const orgId = activeOrganizationid()

const CardEmployeesAttendance = () => {

//  const dispatch = useDispatch()

  const store = useSelector(state => state.notifications)
  const attendanceDate = moment().startOf('day').utc().format('x')
  const [employeesAttendance, setEmployeeAttendances] = useState([])

  const statusArr = ['', 'Early', 'On Time', 'Late']
  const statusColorArr = ['', 'light-primary', 'light-success', 'light-warning']
  const dateFormat = (value) => {
    if (value === null) {
      return '-'
    }
    return moment(value, 'x').format("h:m A")
  }

  useEffect(() => {

    // const pusher = new Pusher("98ccef490e4b3e0578ff", {
    //   cluster: "ap2",
    //   useTLS: true
    // })
    // const channel = pusher.subscribe('Turia')
    // channel.bind(`Turia_${orgId}_notification`, async (data) => {
    //   await dispatch(setNotification(data))
    // })

  }, [])

  useEffect(() => {
    if (store.message !== null) {

      const data = employeesAttendance.map((obj) => {

        const temp = obj
        if (obj.id === store.message.userid) {
          temp['status'] = store.message.status
          temp['punchin'] = store.message.punchin
        }
        return temp
      })

      setEmployeeAttendances(data)
    }
  }, [store.message])

  const renderTasks = () => {
    return employeesAttendance.map((row, k) => {
      return (
        <div key={k} className='employee-task d-flex justify-content-between align-items-center'>
          <div className='d-flex col-md-7'>
            <Avatar initials color={'light-primary'} className='me-75' content={row.name?.charAt(0).toUpperCase()} />
            <div className='my-auto'>
              <h6 className='mb-0 font-small-3'>{row.name?.split(' ')[0]}</h6>
            </div>
          </div>
          <div className='d-flex justify-content-between align-items-center col-md-3 text-start font-small-3'>
            {dateFormat(row.punchin)}
          </div>
          <div className='d-flex justify-content-between align-items-center col-md-2'>
            {row.status !== null ? <Badge color={`${statusColorArr[row.status]}`}>{statusArr[row.status]} </Badge> : '-'
            }
          </div>
        </div>
      )
    })
  }

  const getCurrentTask = () => {

    axios.post('/employeeattendances/dailylist', { date: attendanceDate, flag: true })
      .then((res) => {
        setEmployeeAttendances(res.data.employeeattendances)
      }).catch((err) => {
        console.log(err)
      })

  }

  useEffect(() => {
    getCurrentTask()
  }, [])

  return (
    <Card className='card-employee-task'>
      <CardHeader>
        <CardTitle tag='h4'>Today's Attendance</CardTitle>
      </CardHeader>

      <CardBody className='height-300 overflow-auto'>
        <div className='d-flex justify-content-between align-items-center'>
          <div className='d-flex col-md-7 font-small-3'>
            User
          </div>
          <div className='d-flex justify-content-between align-items-center font-small-3 col-md-3'>
            Punch In
          </div>
          <div className='d-flex justify-content-between align-items-center font-small-3 col-md-2'>
            Status
          </div>
        </div>
        <hr className='my-50'></hr>
        {renderTasks()}
      </CardBody>
    </Card>
  )
}

export default CardEmployeesAttendance
