// ** React Imports
import { Fragment, useState, useEffect } from 'react'
import { feedsData } from './data'
// ** Third Party Components
import axios from '@src/configs/axios/axiosConfig'

// ** Custom Components
import UILoader from '@components/ui-loader'

// ** Reactstrap Imports
import { Row, Col, Button } from 'reactstrap'

// ** Demo Components
import CardEmployeesTask from './CardEmployeesTask'
import CardEmployeesAttendance from './CardEmployeesAttendance'
import ProfilePosts from './ProfilePosts'
import { activeOrganization, orgUserId } from '@src/helper/sassHelper'
const activeOrg = activeOrganization()

// ** Styles
import '@styles/react/pages/page-profile.scss'
import ReactPaginate from 'react-paginate'
const userId = orgUserId()

const Profile = () => {

    const [feedsDataList, setFeedsData] = useState([])
    const [currentPage, setCurrentPage] = useState(1)
    const [totalPage, setTotalPage] = useState(1)
    const rowsPerPage = 50
    const getFeeds = (params) => {

        axios.post('/auditlogs/timeline', params)
            .then((res) => {
                setTotalPage(res.data.auditlogs.total)
                const feeds = res.data.auditlogs.auditlogs.map((obj) => {
                    const data = {}
                    const Component = feedsData[obj.tablename].customContent
                    const Icon = feedsData[obj.tablename].icon
                    data['customContent'] = <Component id={obj.modulereferenceid} event={obj} />
                    data['icon'] = <Icon size={12} />
                    data['post'] = obj
                    return data
                })

                setFeedsData(feeds)
            }).catch(() => {

            })
    }

    useEffect(() => {
        const params = {
            page: 1,
            userId: parseInt(activeOrg.rolelevel) === 1 ? '' : userId
        }
        getFeeds(params)
    }, [])

    const handlePagination = (page) => {
        setCurrentPage(page.selected + 1)
        const params = {
            page: page.selected + 1,
            userId: parseInt(activeOrg.rolelevel) === 1 ? '' : userId
        }
        getFeeds(params)
    }

    const CustomPagination = () => {
        const count = Number(Math.ceil(totalPage / rowsPerPage))

        return (
            <ReactPaginate
                previousLabel={''}
                nextLabel={''}
                pageCount={count || 1}
                activeClassName='active'
                forcePage={currentPage !== 0 ? currentPage - 1 : 0}
                onPageChange={page => handlePagination(page)}
                pageClassName={'page-item'}
                nextLinkClassName={'page-link'}
                nextClassName={'page-item next'}
                previousClassName={'page-item prev'}
                previousLinkClassName={'page-link'}
                pageLinkClassName={'page-link'}
                containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
            />
        )
    }

    return (
        <Fragment>

            <div id='user-profile'>
                <section id='profile-info'>
                    <Row className='d-flex flex-sm-column-reverse flex-lg-row'>
                        <Col lg={{ size: 3, order: 1 }} md={{ size: 3, order: 1 }} sm={{ size: 12, order: 1 }} xs={{ order: 1 }} className='feedcard-right'>
                            <CardEmployeesAttendance />
                        </Col>
                        <Col lg={{ size: 6, order: 2 }} md={{ size: 6, order: 2 }} sm={{ size: 12, order: 3 }} xs={{ order: 3 }} className='m-auto'>
                            <ProfilePosts data={feedsDataList} />
                        </Col>
                        <Col lg={{ size: 3, order: 3 }} md={{ size: 3, order: 3 }} sm={{ size: 12, order: 2 }} xs={{ order: 2 }} className='feedcard-left'>
                            <CardEmployeesTask />
                        </Col>
                    </Row>
                    <Row>
                        <Col className='text-center' sm='12'>
                            <CustomPagination />
                        </Col>
                    </Row>
                </section>
            </div>
        </Fragment>
    )
}

export default Profile
