import Avatar from '@components/avatar'
import pdf from '@src/assets/images/icons/file-icons/pdf.png'
import { useEffect, useState, Fragment } from 'react'
import axios from '@src/configs/axios/axiosConfig'
import classnames from 'classnames'
import moment from 'moment'
import { Badge, Button, Row } from 'reactstrap'
import { Link } from 'react-router-dom'
const profileImg = require('@src/assets/images/avatars/avatar-blank.png').default
export const CardInvoice = ({ id, event }) => {

    const [data, setData] = useState(null)

    useEffect(() => {
        axios.post('/taskinvoices/get', { id })
            .then((res) => {
                setData(res.data.taskinvoices)
            }).catch((err) => {
                console.log(err)
            })

    }, [id])


    return (
        <Fragment>
            <div className='d-flex justify-content-start align-items-center mb-1'>
                <Avatar className='me-1' img={data?.imageid ? `https://drive.google.com/thumbnail?id=${data?.imageid}&authuser=0` : profileImg} imgHeight='50' imgWidth='50' />
                <div className='profile-user-info'>
                    {event.eventtype === 'created' && <Fragment>
                        <p className='mb-0'>{data?.createdBy}</p>
                        <small className='text-muted'>{moment(data?.createdon, 'x').format('MMM DD, y h:m a')}</small>
                    </Fragment>
                    }
                    {event.eventtype === 'updated' && <Fragment>
                        <p className='mb-0'>{data?.updatedBY}</p>
                        <small className='text-muted'>{moment(data?.updatedon, 'x').format('MMM DD, y h:m a')}</small>
                    </Fragment>
                    }
                </div>
            </div>
            <div
                className={classnames('d-flex justify-content-between flex-sm-row flex-column', {
                    'mb-sm-0 mb-1': data?.createdon
                })}
            >
                <p>Invoice {event?.eventtype} for {data?.billingcurrencysymbol} {data?.totalamount} towards {data?.taskinvoiceitemsservicename}</p>
                <span
                    className={classnames('timeline-event-time', {
                        [data?.createdon]: data?.createdon
                    })}
                >
                    {moment(data?.createdon, 'x').format('MMM DD, Y')}
                </span>
            </div>
            <p>{data?.contactname}</p>
            <Button outline color='primary' tag={Link} to={`/${data?.status === 4 ? 'proposal/view' : 'invoice/view'}/${data?.id}`}>View Invoice</Button>

        </Fragment>
    )
}

export const CardPayment = ({ id }) => {

    const [data, setData] = useState(null)

    useEffect(() => {
        axios.post('/taskinvoicepayments/get', { id })
            .then((res) => {
                setData(res.data.taskinvoicepayments)
            }).catch((err) => {
                console.log(err)
            })

    }, [id])

    const downloadPdf = (id) => {
        axios.post('/pdfmaker/pdf', { id })
            .then((res) => {
                const downloadLink = document.createElement("a")
                const fileName = `${data.invoiceno}.pdf`
                downloadLink.href = res.data.pdf
                downloadLink.download = fileName
                downloadLink.click()
                document.removeChild(downloadLink)
            })
            .catch((err) => { console.log(err) })
    }

    return (
        <Fragment>
            <div className='d-flex justify-content-start align-items-center mb-1'>
                <Avatar className='me-1' img={data?.imageid ? `https://drive.google.com/thumbnail?id=${data?.imageid}&authuser=0` : profileImg} imgHeight='50' imgWidth='50' />
                <div className='profile-user-info'>
                    {event.eventtype === 'created' && <Fragment>
                        <p className='mb-0'>{data?.createdBy}</p>
                        <small className='text-muted'>{moment(data?.createdon, 'x').format('MMM DD, y h:m a')}</small>
                    </Fragment>
                    }
                    {event.eventtype === 'updated' && <Fragment>
                        <p className='mb-0'>{data?.updatedBY}</p>
                        <small className='text-muted'>{moment(data?.updatedon, 'x').format('MMM DD, y h:m a')}</small>
                    </Fragment>
                    }
                </div>
            </div>
            <div
                className={classnames('d-flex justify-content-between flex-sm-row flex-column', {
                    'mb-sm-0 mb-1': data?.createdon
                })}
            >
                <p>Payment Received for {data?.billingcurrencysymbol} {data?.totalamountpaid}</p>
                <span
                    className={classnames('timeline-event-time', {
                        [data?.createdon]: data?.createdon
                    })}
                >
                    {moment(data?.createdon, 'x').format('MMM DD, Y')}
                </span>
            </div>
            <p>{data?.contactname}</p>
            <div className='d-flex align-items-center cursor-pointer' onClick={() => { downloadPdf(data?.invoiceid) }}>
                <img className='me-1' src={pdf} alt='pdf' height='23' />
                <span className='text-primary'>{data?.invoiceno}</span>
            </div>

        </Fragment>
    )
}


export const CardTask = ({ id, event }) => {

    const [data, setData] = useState(null)
    const [taskDescription, setTaskDescription] = useState('')
    const statusOptions = ['', 'Pending', 'In progress', 'Completed', 'On Hold', 'Cancelled', 'Sent to Review', 'Request Changes']
    const statusObj = [
        '',
        'light-primary',
        'light-warning',
        'light-success',
        'light-secondary',
        'light-secondary',
        'light-ocean',
        'light-info',
        'light-danger'
    ]
    const getDescription = () => {
        axios.post('/taskconversations/description', { taskId: id })
            .then((res) => {
                setTaskDescription(res.data.taskconversation === undefined ? '' : res.data.taskconversation.comment)
            }).catch((err) => {
                console.log(err)
            })
    }

    useEffect(() => {
        axios.post('/tasks/getbasic', { id })
            .then((res) => {
                setData(res.data.task)
                if (!res.data.recurringflag) {
                    getDescription()
                }
            }).catch((err) => {
                console.log(err)
            })

    }, [id])

    return (
        <Fragment>
            <div className='d-flex justify-content-start align-items-center mb-1'>
                <Avatar className='me-1' img={data?.imageid ? `https://drive.google.com/thumbnail?id=${data?.imageid}&authuser=0` : profileImg} imgHeight='50' imgWidth='50' />
                <div className='profile-user-info'>
                    {event.eventtype === 'created' && <Fragment>
                        <p className='mb-0'>{data?.createdBy}</p>
                        <small className='text-muted'>{moment(data?.createdon, 'x').format('MMM DD, y h:m a')}</small>
                    </Fragment>
                    }
                    {event.eventtype === 'updated' && <Fragment>
                        <p className='mb-0'>{data?.updatedBY}</p>
                        <small className='text-muted'>{moment(data?.updatedon, 'x').format('MMM DD, y h:m a')}</small>
                    </Fragment>
                    }
                </div>
            </div>
            {event.eventtype === 'created' &&
                <span> A new {data?.recurringflag ? 'recurring' : ''} task created</span>}
            {event.eventtype === 'updated' &&
                <span>{data?.recurringflag ? 'recurring' : ''} Task updated</span>}

            <p><span className='text-primary'>{data?.servicename} </span>for {data?.clientname}</p>

            {taskDescription !== '' &&
                <div>{taskDescription}</div>
            }
            <div className='d-flex justify-content-between flex-wrap flex-sm-row flex-column'>
                <div>
                    <span className='text-muted mb-50'>Assignee</span>
                    <div className='d-flex align-items-center'>
                        {
                            data?.assignee.map((obj, k) => {
                                return (
                                    <Badge key={k} color='light-primary' className='text-primary me-50'> {obj.name}</Badge>
                                )
                            })
                        }
                    </div>
                </div>
                <div>
                    <span className='text-muted mb-50'>Reviewer</span>
                    <div className='d-flex align-items-center'>
                        {
                            data?.reviewer.map((obj, k) => {
                                return (
                                    <Badge key={k} color='light-primary' className='text-primary me-50'> {obj.name}</Badge>
                                )
                            })
                        }

                    </div>
                </div>
                {!data?.recurringflag &&
                    <div className='mt-sm-0 mt-1'>
                        <p className='text-muted mb-50'>Status</p>
                        <Badge className='mb-0' color={statusObj[data?.status]}>{statusOptions[data?.taskstatus]}</Badge>
                    </div>
                }
                {!data?.recurringflag &&
                    <div className='mt-sm-0 mt-1'>
                        <p className='text-muted mb-50'>End Date</p>
                        <p className='mb-0'>{moment(data?.enddate, 'x').format('MMM DD, Y')}</p>
                    </div>
                }
            </div>
            {!data?.recurringflag &&

                <Button tag={Link} to={`/task/view/${data?.id}`} outline color='primary'>View Task</Button>

            }
        </Fragment>
    )
}

