// ** React Imports
import { Fragment, useEffect, useState } from 'react'

// ** Custom Components
import Avatar from '@components/avatar'

// ** Third Party Components
//import classnames from 'classnames'
import { Heart, MessageSquare, Share2 } from 'react-feather'
//import AvatarGroup from '@components/avatar-group'
// ** Reactstrap Imports
import { Card, CardBody, CardText, Row, Col, Input, Label, Button } from 'reactstrap'
import axios from '@src/configs/axios/axiosConfig'
import { activeOrganization, orgUserId } from '@src/helper/sassHelper'
import moment from 'moment'
const activeOrg = activeOrganization()
const userId = orgUserId()

const CommentBox = ({ post }) => {

  const [description, setDescription] = useState('')
  const [data, setData] = useState([])

  useEffect(() => {
    if (post.comments) {
      setData(post.comments)
    }
  }, [post])

  const getComment = () => {

    if (post.tablename !== 'tasks' && post.tablename !== 'taskconversations') {
      axios.post('/feedscomment/list', { id: post.modulereferenceid })
        .then((res) => {
          setData(res.data.feedscomment)
        })
    } else {
      axios.post('/taskconversations/list', { taskId: post.modulereferenceid })
        .then((res) => {
          setData(res.data.taskconversations)
        })
    }
  }

  const onSubmit = () => {

    if (post.tablename !== 'tasks' && post.tablename !== 'taskconversations') {
      const obj = { 
        description,
        organizationId: activeOrg.id,
        username: activeOrg.organizationusersname,
        moduleReferenceId: post.modulereferenceid,
        createdBy: userId
      }
      axios.post('/feedscomment/create', obj)
        .then(() => {
          getComment()
          setDescription('')
        })
        .catch(() => { })
    } else {
      const comment = {
        comment: description,
        taskId: post.modulereferenceid,
        createdBy: userId,
        organizationId: activeOrg.id,
        attachmentIds: [],
        isDefault: false
      }
      axios.post('/taskconversations/create', comment)
        .then(() => {
          getComment()
          setDescription('')
        })
        .catch(() => { })
    }
  }

  return (
    <Fragment>
      <Row className='d-flex justify-content-start align-items-center flex-wrap pb-50 post-actions'>
        <Col>
          {/* <AvatarGroup data={avatars} /> */}
        </Col>
        <Col className='d-flex justify-content-between justify-content-sm-end align-items-center' sm='6'>
          <a href='/' className='text-nowrap' onClick={e => e.preventDefault()}>
            <MessageSquare size={18} className='text-body me-50'></MessageSquare>
            <span className='text-muted me-1'>{data?.length}</span>
          </a>
        </Col>
      </Row>
      <Fragment>
        <fieldset className='form-label-group mb-50'>
          <Label className='form-check-label' for={`add-comment-${post?.username}`}>
            Add Comment
          </Label>
          <Input id={`add-comment-${post?.username}`} value={description} type='textarea' rows='3' placeholder='Add Comment' onInput={(e) => { setDescription(e.target.value) }} />
        </fieldset>
        <Button color='primary' size='sm' onClick={() => onSubmit()}>
          Post Comment
        </Button>
      </Fragment>
      {data?.map(comment => (
        <div key={comment.creatorname} className='d-flex align-items-start mt-1'>
          <Avatar img={comment.avatar} className='mt-25 me-75' imgHeight='34' imgWidth='34' />
          <div className='profile-user-info w-100'>
            <div className='d-flex align-items-center justify-content-between'>
              <h6 className='mb-0'>{comment.creatorname}</h6>
              {/* <a href='/' onClick={e => e.preventDefault()}>
                <Heart
                  size={18}
                  className={classnames('text-body', {
                    'profile-likes': comment.youLiked === true
                  })}
                />
              </a> */}
              <span className='font-small-2'>{moment(comment.createdon, 'x').format('hh:mm a')}</span>
            </div>
            <small>{comment.comment}</small>
          </div>
        </div>
      ))}
    </Fragment>
  )

}

const ProfilePosts = ({ data }) => {

  return (
    <div>
      {
        data.map((item, k) => {
          return (
            <Card className='post' key={k}>
              <CardBody>
                <CardText>{item?.customContent}</CardText>
                <CommentBox post={item.post} key={k} />
              </CardBody>
            </Card>
          )
        })
      }
    </div>
  )
}
export default ProfilePosts
